const logRegex = {
  status: /^.{14} Status(?<type>Add|Remove) 1[AE]:(?<statusID>[^:]*):(?<statusName>[^:]*):[^:]*:[^:]*:[^:]*:(?<targetID>[^:]+):/,
  chat: new RegExp("^.{14} ChatLog 00:00(?:10|38):[^:]*:/card"),
  action: /^.{14} (?:AOE)?ActionEffect 1[56]:(?<casterID>1.{7}):[^:]+:(?<actionID>[^:]*):[^:]*:/,
  drawCard: /^.{14} ActionEffect 15:(?<casterID>1.{7}):[^:]+:E06:/,
  updateHp: /^.{14} UpdateHp 27:(?<targetID>[^:]+):[^:]+:(?<currentHP>\d+):/,
  death: /^.{14} Death 19:(?<targetID>[^:]+):/,
  director: /^.{14} Director 21:.{8}:4000000F/,
  territory: /^.{14} Territory 01:/,
};

let partyData: any;
let playerId: any = null;
let playerJob = "AST";
let playerJobDetail: any = { heldCard: "None", arcanums: [] };
let lastestAstrodyneCast = 0;
let inCombat = false;
const redrawReady = () => {
  return CONFIG.value.redraw && (partyData.partyStatus[0]?.has(2713) ?? false);
};
const arcanumsRepeat = () => {
  switch (playerJobDetail.arcanums.length) {
    case 1:
      return playerJobDetail.arcanums.includes(cardsMap?.[playerJobDetail.heldCard as keyof typeof cardsMap]?.seal);
    case 2:
      const card = cardsMap?.[playerJobDetail.heldCard as keyof typeof cardsMap];
      if (
        card &&
        playerJobDetail.arcanums.includes(cardsMap?.[playerJobDetail.heldCard as keyof typeof cardsMap]?.seal) &&
        ((getMaxDps("melee").max > getMaxDps("range").max * 1.25 && card.bonus === "range") ||
          (getMaxDps("range").max > getMaxDps("melee").max * 1.25 && card.bonus === "melee"))
      ) {
        return false;
      } else {
        return playerJobDetail.arcanums.includes(cardsMap?.[playerJobDetail.heldCard as keyof typeof cardsMap]?.seal);
      }
    default:
      return false;
  }
};
const astrodyneReady = () => CONFIG.value.astrodyne && playerJobDetail.arcanums.length === 3 && (new Date().getTime() - lastestAstrodyneCast) / 1000 > 14;
const iHaveDivination = () => partyData.partyStatus[0]?.has(1878);

function getPlayerIdHex() {
  return playerId?.toString(16)?.toUpperCase();
}

function initData() {
  data.value.dps.forEach((job) => {
    if (job.jobSrc === "") job.jobSrc = getClassjobIconSrc(Util.jobToJobEnum(job.job as Job));
    if (!job.processed) {
      let i = 0;
      let arr = [];
      let step = Math.ceil(job.cycle.length / 24);
      while (i < job.cycle.length) {
        // @ts-ignore
        arr.push(job.cycle.slice(i, (i += step)).reduce((p, c) => p + c, 0) / step + job.cdDps);
      }
      job.cycle = [];
      let total = arr.reduce((pre, cur) => pre + cur, 0);
      for (let i = 0; i < arr.length; i++) {
        job.cycle[i] = parseFloat((arr[i] / (total / arr.length)).toFixed(2));
      }
      job.processed = true;
    }
  });
  partyData = {
    party: [],
    partyID: [],
    partyName: [],
    partyJob: [],
    partySync: [],
    partyDps: [],
    partyStatus: [new Map()],
    partyType: null,
    partyIsDead: [],
  };
}

function clearData() {
  partyData.partyDps =
    partyData.party.length > 0
      ? // @ts-ignore
        partyData.party.map((p) => p.job).map((j) => data.value.dps.find((k) => k.job === jobs[j])?.cdDps ?? 0)
      : [0];
  partyData.partySync = partyData.party.length > 0 ? partyData.party.map((_: any) => null) : [null];
  partyData.partyStatus = partyData.party.length > 0 ? partyData.party.map((_: any) => new Map()) : [new Map()];
  partyData.partyIsDead = partyData.party.length > 0 ? partyData.party.map((_: any) => false) : [false];
}

function doAction(str: string) {
  const queue = [];
  for (let i = 0; i < CONFIG.value.queueTimes; i++) {
    queue.push({ c: "DoTextCommand", p: `/ac ${str}`, d: i && 50 });
  }
  callOverlayHandler({
    call: "PostNamazu",
    c: "DoQueueActions",
    p: JSON.stringify(queue),
  });
}

function getMaxDps(bonus: any) {
  const res: any = [];
  for (let i = 0; i < partyData.party.length; i++) {
    let dps = partyData.partyDps[i];
    if (partyData.partySync[i]) {
      const time = (new Date().getTime() - partyData.partySync[i]) / 1000;
      const cycle = partyData.jobDetail[i];
      let arr = [...cycle.cycle, ...cycle.cycle];
      arr.slice(Math.round(((time % cycle.cycleTime) / cycle.cycleTime) * cycle.cycle.length), Math.round(15 / (cycle.cycleTime / cycle.cycle.length)));
      dps = dps * (arr.reduce((p, v) => p + v) / arr.length);
    }
    if (playerJobDetail && bonus !== partyData.jobDetail[i].type) dps = 0.2 * dps;
    const status = partyData.partyStatus[i];
    for (const item of status) {
      const byName = debuffs.keyWords.find((v) => v.words.includes(item[1]))?.efficacy ?? 1;
      const byID = debuffs.keyDebuffs.find((v) => v.ids.includes(item[0]))?.efficacy ?? 1;
      dps = dps * byID * byName;
    }
    res.push(dps);
  }
  // @ts-ignore
  const index = res.reduce((pre, cur, index) => (cur > res[pre] ? index : pre), 0);
  // @ts-ignore
  const max = res.reduce((pre, cur) => (pre > cur ? pre : cur), 0);
  return { index, max, res };
}

function handleOnLogEvent(e: any) {
  if (playerJob !== "AST") return;
  for (const log of e.detail.logs) {
    let re;
    if ((re = log.match(logRegex.status))) {
      if (re.groups.type === "Add") {
        const partyIndex = re.groups.targetID === getPlayerIdHex() ? 0 : partyData.partyID.indexOf(re.groups.targetID);
        if (partyIndex > -1) partyData.partyStatus[partyIndex].set(parseInt(re.groups.statusID, 16), re.groups.statusName);
      } else if (re.groups.type === "Remove") {
        const partyIndex = re.groups.targetID === getPlayerIdHex() ? 0 : partyData.partyID.indexOf(re.groups.targetID);
        if (partyIndex > -1) partyData.partyStatus[partyIndex].delete(parseInt(re.groups.statusID, 16));
      }
    } else if (logRegex.chat.test(log)) {
      if (playerJobDetail.heldCard === "None") {
        if (iHaveDivination() && astrodyneReady()) doAction(astrodyneActionName[CONFIG.value.lang]);
        else doAction(drawActionName[CONFIG.value.lang]);
      } else if (arcanumsRepeat() && redrawReady()) {
        doAction(redrawActionName[CONFIG.value.lang]);
      } else if (inCombat) {
        if (astrodyneReady()) doAction(astrodyneActionName[CONFIG.value.lang]);
        else
          doAction(
            playActionName[CONFIG.value.lang] +
              " <" +
              ((partyData.party.length > 1 ? getMaxDps(cardsMap?.[playerJobDetail?.heldCard as keyof typeof cardsMap]?.bonus).index : 0) + 1) +
              ">",
          );
      } else if (redrawReady()) {
        doAction(redrawActionName[CONFIG.value.lang]);
      } else if (!inCombat) {
        callOverlayHandler({ call: "PostNamazu", c: "DoTextCommand", p: `/e 没进战斗，发个锤子<se.11>` });
      } else {
        doAction(astrodyneActionName[CONFIG.value.lang]);
      }
    } else if (logRegex.director.test(log) || logRegex.territory.test(log)) {
      clearData();
    } else if ((re = log.match(logRegex.action))) {
      const actorIndex = re.groups.targetID === getPlayerIdHex() ? 0 : partyData.partyID.indexOf(re.groups.casterID);
      if (actorIndex > -1) {
        if (partyData.jobDetail?.[actorIndex]?.action?.toString(16).toUpperCase() === re.groups.actionID)
          partyData.partySync[actorIndex] = new Date().getTime();
        if (actorIndex === 0 && re.groups.actionID === Number(25870).toString(16).toUpperCase()) lastestAstrodyneCast = new Date().getTime();
      }
    } else if ((re = log.match(logRegex.updateHp))) {
      const actorIndex = re.groups.targetID === getPlayerIdHex() ? 0 : partyData.partyID.indexOf(re.groups.targetID);
      if (actorIndex > -1) {
        partyData.partyIsDead[actorIndex] = Number(re.groups.currentHP) === 0;
      }
    } else if ((re = log.match(logRegex.death))) {
      const actorIndex = re.groups.targetID === getPlayerIdHex() ? 0 : partyData.partyID.indexOf(re.groups.targetID);
      if (actorIndex > -1) {
        partyData.partyIsDead[actorIndex] = true;
      }
    }
  }
}

function handlePlayerChangedEvent(e: any) {
  playerJob = e.detail.job;
  playerId = e.detail.id;
  if (e.detail.job === "AST") playerJobDetail = e.detail.jobDetail;
}

function handlePartyChanged(e: any) {
  new Promise((resolve) => {
    (function waitFor() {
      if (playerId !== null || e.party.length === 0) return resolve(undefined);
      setTimeout(waitFor, 500);
    })();
  }).then(() => {
    partyData.party = e.party
      .filter((v: any) => v.inParty)
      .sort((a: any, b: any) => {
        if (a.id === getPlayerIdHex()) return -1;
        else if (b.id === getPlayerIdHex()) return 1;
        if (a.job === b.job) return parseInt(a.id, 16) - parseInt(b.id, 16);
        return computedJob.value.indexOf(a.job) - computedJob.value.indexOf(b.job);
      });
    partyData.partyID = partyData.party.map((v: any) => v.id);
    partyData.partyName = partyData.party.map((v: any) => v.name);
    partyData.partyJob = partyData.party.map((v: any) => v.job);
    partyData.jobDetail = partyData.party
      .map((v: any) => v.job)
      // @ts-ignore
      .map((v: any) => data.value.dps.find((j) => j.job === jobs[v]) ?? {});
    clearData();
  });
}

function handleCombatData(e: any) {
  if (playerJob !== "AST" || e.isActive === "false" || Number(e.Encounter.DURATION) < 30) return;
  for (let i = 0; i < partyData.party.length; i++) {
    const dps = e.Combatant?.[i === 0 ? "YOU" : partyData.partyName[i]]?.["ENCDPS"] ?? 0;
    partyData.partyDps[i] = isNaN(dps) ? 0 : Number(dps);
  }
}

function handleInCombatChanged(e: any) {
  inCombat = e.detail.inGameCombat;
}

function onBlurEvent(event: FocusEvent): void {
  checkInput();
}

function onInputEvent(value: string | number): void {
  CONFIG.value.partySort = value.toString().replace(new RegExp(`[^${JOBS}]`, "g"), "");
  checkInput();
}

function checkInput() {
  if (CONFIG.value.partySort.length === 0) CONFIG.value.partySort = JOBS;
  CONFIG.value.partySort = CONFIG.value.partySort
    .split("")
    .filter((v, i, a) => a.indexOf(v) === i)
    .join("");
  inputAlertText.value = "";
  Array.from(JOBS).forEach((element) => {
    if (!CONFIG.value.partySort.includes(element)) {
      inputAlertText.value += element;
    }
  });
}

function restoreSetting() {
  ElMessageBox.confirm("确定要使表格内填写的DPS数据全部恢复默认值吗？", "警告", {
    confirmButtonText: "确定",
    cancelButtonText: "取消",
    type: "warning",
  })
    .then(() => {
      localStorage.removeItem("ok-ast-card-dps");
      location.reload();
    })
    .catch(() => {});
}

function handleInputCycle(row: { cycle: string; job: string }) {
  ElMessageBox.prompt("请输入一个数字数组以逗号分隔", "编辑周期", {
    confirmButtonText: "确定",
    cancelButtonText: "取消",
    inputPattern: /([\d.]+,)*[\d.]+$/,
    inputErrorMessage: "验证未通过/([\\d.]+,)*[\\d.]+$/",
    inputValue: row.cycle,
  })
    .then(({ value }) => {
      const j = data.value.dps.find((v) => v.job === row.job);
      if (j) {
        j.cycle = value
          .replace(/[^\d,]/, "")
          .split(",")
          .map((v) => Number(v));
      }
    })
    .catch(() => {});
}

function copyMacro(text: string) {
  navigator.clipboard.writeText(text).catch(() => {
    const input = document.createElement("input");
    input.value = text;
    document.body.appendChild(input);
    input.select();
    document.execCommand("copy");
    document.body.removeChild(input);
  });
  ElMessage.success("已复制到剪贴板");
}

initData();
checkInput();
addOverlayListener("onPlayerChangedEvent", handlePlayerChangedEvent);
addOverlayListener("onLogEvent", handleOnLogEvent);
addOverlayListener("PartyChanged", handlePartyChanged);
addOverlayListener("CombatData", handleCombatData);
addOverlayListener("onInCombatChangedEvent", handleInCombatChanged);
startOverlayEvents();

</script>

<template>
  <el-card shadow="never">
    <el-form :model="CONFIG" label-width="120px" label-position="left">
      <el-form-item label="客户端语言">
        <el-select v-model="CONFIG.lang">
          <el-option v-for="item in langOptions" :key="item.value" :label="item.label" :value="item.value" />
        </el-select>
      </el-form-item>
      <el-popover ref="popover" placement="bottom-end" title="注意" :width="200" trigger="hover" content="必须与游戏内小队排序保持一致">
        <template #reference>
          <el-form-item label="小队排序" style="width: 30rem">
            <el-input clearable v-model.trim="CONFIG.partySort" :placeholder="JOBS" @blur="onBlurEvent" @input="onInputEvent" />
            <el-alert v-show="inputAlertText.length > 0" :closable="false" :title="`缺少职业：${inputAlertText}`" type="error" show-icon />
          </el-form-item>
        </template>
      </el-popover>
      <el-form-item label="接管能力技">重抽 <el-switch v-model="CONFIG.redraw" /> 星力 <el-switch v-model="CONFIG.astrodyne" /></el-form-item>
      <el-form-item label="每次调用/ac次数">
        <el-input-number v-model="CONFIG.queueTimes" :min="0" :max="10" :step="1" :step-strictly="true" size="small" />
      </el-form-item>
    </el-form>
    <el-table v-if="showConfig" :data="data.dps" style="width: 100%" height="433" m-b-3 border>
      <el-table-column label="职业" width="110">
        <template #default="scope">
          <img loading="lazy" :src="scope.row.jobSrc" :alt="scope.row.job" srcset="" style="height: 1.25rem" />
          {{ Util.nameToFullName(scope.row.job).cn }}
        </template>
      </el-table-column>
      <el-table-column label="近/远" width="100">
        <template #default="scope">
          <el-select v-model="scope.row.type" placeholder="无">
            <el-option v-for="item in cardTypeOptions" :key="item.value" :label="item.label" :value="item.value" />
          </el-select>
        </template>
      </el-table-column>
      <el-table-column label="循环标志ID" width="110">
        <template #default="scope">
          <el-input-number v-model="scope.row.action" :min="0" :max="100000" :step="1" :step-strictly="true" :controls="false" style="width: 5rem" />
        </template>
      </el-table-column>
      <el-table-column label="循环周期" width="110">
        <template #default="scope">
          <el-input-number v-model="scope.row.cycleTime" :min="0" :max="600" :step="0.5" :step-strictly="true" :controls="false" style="width: 5rem" />
        </template>
      </el-table-column>
      <el-table-column label="周期曲线">
        <template #default="scope">
          <el-button @click="handleInputCycle(scope.row)">编辑</el-button>
          {{ scope.row.cycle }}
        </template>
      </el-table-column>
    </el-table>
    <el-button v-if="!showConfig" @click="toggleConfig()" type="primary">展开详细DPS设置</el-button>
    <el-button v-if="showConfig" @click="toggleConfig()" type="primary">收起</el-button>
    <el-button v-if="showConfig" type="warning" @click="restoreSetting">恢复默认</el-button>
    <el-divider />
    <div>
      <p>依赖<a href="https://github.com/Natsukage/PostNamazu" target="_blank">鲶鱼精邮差</a>v1.3.2.1+</p>
      <p>
        选择一个宏进行控制 <el-button type="info" plain bg @click="copyMacro('/e /card')">默语</el-button>
        <el-button type="success" plain bg @click="copyMacro('/l1 /card')">本地通讯贝1</el-button
        >（使用本地通讯贝1可以把它放到不常用的聊天窗口分类中以避免刷屏）
      </p>
      <p>修改好设置之后，在ACT取消勾选“显示悬浮窗”以后台运行，如果需要修改设置重新勾选以显示本页面。</p>
      <p>常见问题：我怎么改不了小队排序：先切到act，再点击悬浮窗才可以正确获得焦点。同时你只能编辑输入规则内的文字。</p>
    </div>
  </el-card>
</template>
